/* everything that will be reused */
:root {
  --pale-blue: hsl(225, 100%, 94%);
  --bright-blue: hsl(245, 75%, 52%);
  --very-pale-blue: hsl(225, 100%, 98%);
  --desaturated-blue: hsl(224, 23%, 55%);
  --dark-blue: hsl(223, 47%, 23%);
}

body {
  font-family: "Red Hat Display", sans-serif;
  background-color: var(--pale-blue);
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

img {
  max-width: 100%;
}

button {
  font-family: inherit;
}

p {
  font-size: 16px;
  line-height: 1.6;
  color: var(--desaturated-blue);
  margin: 0;
}

h2 {
  font-size: 16px;
  margin: 0 0 0 0;
}

/* background image */

.bg-image {
  background: url(../images/pattern-background-mobile.svg) no-repeat
    center/cover;
  height: 400px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}

.btn {
  border: none;
  padding: 5px 10px;
  border-radius: 10px;
  font-weight: bold;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.btn-change {
  background-color: transparent;
  color: var(--secondary-bright-blue);
  text-decoration: underline;
}

.btn-primary {
  background-color: var(--bright-blue);
  color: #fff;
  padding: 10px;
  margin: 10px;
}

.btn-primary:hover,
.btn-change {
  opacity: 0.8;
}

.btn-secondary {
  background-color: transparent;
  color: var(--desaturated-blue);
}

.btn-secondary:hover {
  color: hsl(223, 47%, 23%);
}

/* main div */
.container {
  margin: 20px;
  padding-bottom: 20px;
  background-color: #fff;
  border-radius: 15px;
  box-shadow: 2px 5px 5px var(--desaturated-blue);
}

.container p.para {
  padding: 0 30px;
}

.container h1 {
  color: hsl(223, 47%, 23%);
  margin: 10px 0;
}

.container img.hero {
  width: 100%;
  border-radius: 15px 15px 0 0;
}

.container .prices {
  margin: 30px auto;
  padding: 10px;
  background-color: var(--pale-blue);
  width: 90%;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.container .prices article {
  display: flex;
  align-items: center;
  justify-content: center;
}

.container .buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.container .prices article img {
  margin-right: 10px;
}

/* Attribution */

.attribution {
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}

@media screen and (min-width: 768px) {
  .bg-image {
    background: url(../images/pattern-background-desktop.svg) no-repeat
      center/cover;
    height: 50vh;
  }

  .container {
    width: 500px;
  }
}
